import React, { useEffect, useContext } from "react";
import logo from "../images/logo/logo.png";
import { GlobalContext } from "../context/GlobalState";
import { Link, useHistory, withRouter, Redirect } from "react-router-dom";
import { useForm } from "react-hook-form";
// import { object, ref, string } from "yup";
// import { yupResolver } from "@hookform/resolvers/yup";

const Register = () => {
  const { client_isLoggedIn, client_info, userRegistration } =
    useContext(GlobalContext);

  let history = useHistory();

  const { register, handleSubmit, errors, reset } = useForm();

  const onSubmit = (data) => {
    userRegistration(data);
    reset();
  };

  useEffect(() => {
    console.log(history.location.pathname);

    // eslint-disable-next-line
  }, []);

  if (client_isLoggedIn && typeof client_info !== "undefined") {
    let to = "/";

    if (client_info.access_times < 2) {
      to = "/subscription";
    }
    return <Redirect to={to} />;
  } else {
    return (
      <div className="bg-super fl-wdt-hgt">
        <div className="content-opt-sz">
          <div className="user-section">
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-12">
                  <div className="logo">
                    <Link to="/">Home</Link>
                  </div>
                </div>
              </div>
              <form autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
                <div className="row">
                  <div className="col-md-12">
                    <label>Nickname</label>
                    <input
                      className="user-inputs"
                      name="alias"
                      placeholder="Choose a username"
                      type="text"
                      ref={register({ required: true })}
                    />
                    <span className="err_msg">
                      {errors.alias && errors.alias.message}
                    </span>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <label>Email address</label>
                    <input
                      className="user-inputs"
                      name="email"
                      placeholder="Your email address"
                      type="text"
                      ref={register({ required: true })}
                    />
                    <span className="err_msg">
                      {errors.email && errors.email.message}
                    </span>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <label>Password</label>
                    <input
                      className="user-inputs"
                      name="password"
                      placeholder="Your password"
                      type="password"
                      ref={register({ required: true })}
                    />
                    <span className="err_msg">
                      {errors.password && errors.password.message}
                    </span>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <label>Confirm Password</label>
                    <input
                      className="user-inputs"
                      name="password"
                      placeholder="Confirm your password"
                      type="password"
                      ref={register({ required: true })}
                    />
                    <span className="err_msg">
                      {errors.password && errors.password.message}
                    </span>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <span className="terms">
                      <input
                        className="agree"
                        type="checkbox"
                        ref={register({ required: true })}
                      />
                      &nbsp;&nbsp;I agree to PDFBookstore's&nbsp;
                      <Link>Terms {"&"} Conditions</Link>&nbsp;and&nbsp;
                      <Link>Privacy Policy</Link>&nbsp;and that i am above{" "}
                      <b>18 years</b> of age
                    </span>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <input
                      className="user-input-btn hght-45"
                      type="submit"
                      value="CREATE ACCOUNT"
                    />
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <p>
                      Already have an account? <Link to="/login">Login</Link>
                    </p>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
};

export default withRouter(Register);
