import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useForm } from "react-hook-form";

const MasterCard = () => {
  const { register, handleSubmit, errors, reset } = useForm();

  const onSubmit = (data) => {
    console.log(data);
  };

  return (
    <form autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
      <div className="row">
        <div className="col-6 col-md-12">
          <label>Cardholder's number</label>
          <div className="pay-box">
            <input
              ref={register({ required: true })}
              type="text"
              name="card-name"
              placeholder=""
            />
            {/* <FontAwesomeIcon icon={faVisa} /> */}
            <FontAwesomeIcon icon={["fab", "cc-mastercard"]} />
          </div>
        </div>
        <div className="col-6 col-md-12">
          <label>Card number</label>
          <div className="pay-box">
            <input
              ref={register({ required: true })}
              type="text"
              name="card-number"
              placeholder=""
            />
            &nbsp;
          </div>
        </div>
        <div className="col-6 col-md-3">
          <label>Expiry</label>
          <div className="pay-box">
            <input
              ref={register({ required: true })}
              type="text"
              name="card-expiry"
              placeholder="MM/YY"
            />
            &nbsp;
          </div>
        </div>
        <div className="col-6 col-md-3">
          <label>CVV</label>
          <div className="pay-box">
            <input
              ref={register({ required: true })}
              type="text"
              name="card-cvv"
              placeholder="CVV"
            />
            &nbsp;
          </div>
        </div>
        <div className="col-6 col-md-6">
          <p>
            * CVV or CVC is the card security code, unique three digits number
            on the back of your card separate from its number.
          </p>
        </div>
      </div>
      <div className="row">
        <div className="col-6 col-md-6">
          <button type="submit">Complete</button>
        </div>
      </div>
    </form>
  );
};

export default MasterCard;
