import React from "react";
import {
  ChevronsLeft,
  ChevronLeft,
  ChevronRight,
  ChevronsRight,
} from "react-feather";
import _ from "lodash";

const Pagination = ({ books, limit }) => {
  const number_of_pages = Math.round(books.length / limit);

  return (
    <>
      {number_of_pages.length > 1 && (
        <div className="pagination">
          <ul>
            <li>
              <button>
                <ChevronsLeft size={15} />
              </button>
              <button>
                <ChevronLeft size={15} />
              </button>
              {_.range(1, number_of_pages).map((_n, i) => (
                <button value={_n} key={i}>
                  {_n}
                </button>
              ))}
              <button>
                <ChevronRight size={15} />
              </button>
              <button>
                <ChevronsRight size={15} />
              </button>
            </li>
          </ul>
        </div>
      )}
    </>
  );
};

export default Pagination;
