import React, { useState, useContext } from "react";
import { GlobalContext } from "../context/GlobalState";
import { ArrowLeft, ArrowRight } from "react-feather";
import { Document, Page, pdfjs } from "react-pdf";
import pdf from "../library/1md9o/1md9o.pdf";
import { Redirect } from "react-router-dom";
import Navigation from "../components/layout/Navigation";
import Footer from "../components/layout/Footer";

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const ReaderPDF = () => {
  const { client_isLoggedIn, client_info } = useContext(GlobalContext);

  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  const onDocumentLoadSuccess = ({ numPages }) => {
    setNumPages(numPages);
  };

  const nextPage = () => {
    if (pageNumber < numPages) {
      setPageNumber(pageNumber + 1);
    }
  };

  const prevPage = () => {
    if (pageNumber > 1) {
      setPageNumber(pageNumber - 1);
    }
  };

  if (client_isLoggedIn && client_info.account_status > 0) {
    return (
      <div className="content">
        <Document
          file={pdf}
          onLoadSuccess={onDocumentLoadSuccess}
          onContextMenu={(e) => e.preventDefault()}
          className="pdf-container"
        >
          <Page pageNumber={pageNumber} />
        </Document>
        <div className="controls">
          <button onClick={prevPage} disabled={pageNumber === 1}>
            <ArrowLeft size={15} />
            &nbsp;Prev
          </button>
          <button onClick={nextPage} disabled={pageNumber === numPages}>
            Next&nbsp;
            <ArrowRight size={15} />
          </button>
        </div>
      </div>
    );
  } else {
    return (
      <div className="bg-grey fl-wdt-hgt" id="startHere">
        <Navigation />
        <div className="content-opt-sz py-3 px-1 bg_bdr">
          <div className="container-fluid profile_box">
            <div className="row">
              <div className="col-lg-12 col-md-12 col-12">
                Cannot open the book ensure you have an active subscription
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
};

export default ReaderPDF;
