import React, { useRef, useEffect, useState, useContext } from "react";
import { Link } from "react-router-dom";
import { GlobalContext } from "../../context/GlobalState";
import logo from "../../images/logo/logo.jpg";
import {
  BookOpen,
  ChevronDown,
  ChevronUp,
  User,
  LogOut,
  Menu,
  X,
  Search,
} from "react-feather";
import user from "../../images/profile.jpg";
import {
  sanitizeBookName,
  capitalizeFirstLetter,
} from "../../helpers/Functions";
import { useLocation } from "react-router-dom";
import _ from "lodash";

const BottomNavigation = () => {
  const { pathname } = useLocation();

  const node = useRef();

  const {
    client_isLoggedIn,
    client_info,
    books,
    categories,
    fetchBooks,
    fetchCategories,
    handleLogout,
  } = useContext(GlobalContext);

  const [menuOpen, setmenuOpen] = useState(false);

  const [state, setstate] = useState({
    suggestions: [],
    search: "",
  });

  const [searchCategory, setsearchCategory] = useState(null);

  const [show_more_links, setshow_more_links] = useState(false);

  const handleClick = (e) => {
    if (!node.current.contains(e.target)) {
      setshow_more_links(false);
      return;
    }
  };

  const searchTermChanged = (e) => {
    const { name, value } = e.target;

    let suggestions = [];

    const terms = books
      .filter(function (book) {
        if (searchCategory !== null) {
          return book.category_id === parseInt(searchCategory);
        } else {
          return books;
        }
      })
      .map((book) => {
        let filter;

        filter = book.title + "|" + book.code;

        return filter;
      });

    if (value.length > 0) {
      const regex = new RegExp(`^${value}`, "i");

      if (terms.length > 0) {
        suggestions = terms.sort().filter((v) => regex.test(v));
      }
    }

    setstate({
      [name]: value,
      suggestions,
    });
  };

  const extractCode = (entry) => {
    const filter = entry.split("|");

    return filter[1];
  };

  const extractTitle = (entry) => {
    const filter = entry.split("|");

    return filter[0];
  };

  const bookSuggestions = () => {
    if (suggestions.length === 0 && search > 0) {
      return <>No results</>;
    } else {
      return (
        <>
          {suggestions.slice(0, 15).map((s, i) => (
            <li onClick={() => enterSelected(extractTitle(s))} key={i}>
              <Link
                to={`/book/${extractCode(s)}/${sanitizeBookName(
                  extractTitle(s)
                )}`}
              >
                <BookOpen size={16} />
                &nbsp;&nbsp;{extractTitle(s)}
              </Link>
            </li>
          ))}
          {suggestions.length > 15 && (
            <li>
              <Link>show all results</Link>
            </li>
          )}
        </>
      );
    }
  };

  const enterSelected = (suggestion) => {
    setstate({
      search: suggestion,
      suggestions: [],
    });
  };

  useEffect(() => {
    fetchCategories();
    fetchBooks();

    return () => {};
    // eslint-disable-next-line
  }, [pathname]);

  useEffect(() => {
    fetchCategories();
    fetchBooks();

    document.addEventListener("mousedown", handleClick);

    return () => {
      document.removeEventListener("mousedown", handleClick);
    };

    // eslint-disable-next-line
  }, []);

  const { suggestions, search } = state;

  const checkToSetRedirect = () => {
    const section = pathname.split("/");

    if (!_.isEmpty(section[1]) && section[1] === "book") {
      localStorage._actRed = JSON.stringify(`book/${section[2]}/${section[3]}`);
    }
  };

  return (
    <>
      <div className="bottom">
        <div className="logo">
          <Link to="">
            <img src={logo} alt="" />
          </Link>
        </div>
        <form className="search">
          <section className="search-box">
            <input
              type="text"
              name="search"
              placeholder={
                searchCategory !== null
                  ? "Search book in category"
                  : "Search book title"
              }
              value={search}
              onChange={(e) => {
                e.preventDefault();

                console.log(e.target.value);

                searchTermChanged(e);
              }}
              // onClick={(e) => searchTermChanged(e)}
            />
            {/* Consider adding a spinner */}
            {/* <button>
          <Search size={18} />
        </button> */}
            <select
              onChange={(e) => {
                e.preventDefault();

                setstate({
                  search: "",
                  suggestions: [],
                });

                setsearchCategory(e.target.value > 0 ? e.target.value : null);
              }}
            >
              <option value="0">All categories</option>
              {categories
                .sort((a, b) => a.name.localeCompare(b.name))
                .map((c, i) => (
                  <option key={i} value={c.id}>
                    {capitalizeFirstLetter(`${c.name}`)}
                  </option>
                ))}
            </select>
          </section>
          <ul className="suggestions">{bookSuggestions()}</ul>
        </form>
        {client_isLoggedIn && (
          <div className="user">
            <div className="user-section">
              <div
                className="user_img"
                style={{
                  backgroundImage: `linear-gradient(rgba(255, 255, 255, 0.0), rgba(255, 255, 255, 0.0)),url(${user})`,
                  backgroundRepeat: "no-repeat",
                  backgroundSize: "cover",
                  backgroundPosition: "center",
                }}
              ></div>
              <p>
                <button onClick={() => setshow_more_links(!show_more_links)}>
                  {client_info.nickname}&nbsp;
                  {show_more_links ? (
                    <ChevronUp size={15} />
                  ) : (
                    <ChevronDown size={15} />
                  )}
                </button>
              </p>
            </div>
            <div
              className={`more-links ${show_more_links ? "" : "hide"}`}
              ref={node}
            >
              <ul>
                <li>
                  <Link to="/profile">
                    <User size={15} />
                    &nbsp;Profile
                  </Link>
                </li>
                <li>
                  <button onClick={() => handleLogout()}>
                    <LogOut size={15} />
                    &nbsp;Logout
                  </button>
                </li>
              </ul>
            </div>
          </div>
        )}
        {!client_isLoggedIn && (
          <>
            <div className="actions" ref={node}>
              <Link to="/login" onClick={() => checkToSetRedirect()}>
                Login
              </Link>
              <Link to="/register" onClick={() => checkToSetRedirect()}>
                Register
              </Link>
            </div>
          </>
        )}
      </div>
      <div className="mobile-bottom">
        <div className="logo-prof">
          <div className="logo">
            <Link to="">
              <img src={logo} alt="" />
            </Link>
          </div>
          <div className="menu">
            {menuOpen ? (
              <X onClick={() => setmenuOpen(false)} size={40} />
            ) : (
              <Menu onClick={() => setmenuOpen(true)} size={40} />
            )}
          </div>
        </div>
        {menuOpen && (
          <ul>
            <li>Profile</li>
            <li>Logout</li>
          </ul>
        )}
        <div className="search-box">
          <div className="actual-search">
            <button>
              <Search size={20} />
            </button>
            <input type="text" placeholder="Search for book by title" />
          </div>
        </div>
      </div>
    </>
  );
};

export default BottomNavigation;
