import React, { createContext, useReducer, useEffect } from "react";
import AppReducer from "./AppReducer";
import axios from "axios";
import {
  encryptData,
  decryptData,
  readJwt,
  baseURL,
} from "../helpers/Functions";
import _ from "lodash";
import { useHistory } from "react-router-dom";

const initialState = {
  registration: true,
  subscription: false,
  loading: false,
  isLoggedIn: false,
  client_isLoggedIn: undefined,
  client_info: undefined,
  book: {},
  books: [],
  books_by_category: [],
  uploader_books: [],
  categories: [],
  payment_status: 0,
  search_list: [],
};

export const GlobalContext = createContext(initialState);

export const GlobalProvider = ({ children }) => {
  const history = useHistory();

  const [state, dispatch] = useReducer(AppReducer, initialState);

  // Write all functions here
  async function loginStatus() {
    let status;

    if (localStorage.token) {
      status = true;
    } else {
      status = false;
    }
    dispatch({
      type: "CHANGE_LOGIN_STATUS",
      payload: status,
    });
  }

  async function logOutAdmin() {
    try {
      localStorage.clear();
      window.location.reload();

      setTimeout(function () {
        loginStatus();
      }, 200);
    } catch (error) {
      console.log(error.message);
    }
  }

  async function loginAdmin(inputs) {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };

      const res = await axios.post(`${baseURL}/admin/login`, inputs, config);

      if (!res.data.error) {
        localStorage.token = res.data.data.token;
        loginStatus();
      }
    } catch (error) {
      console.log(error.message);
    }
  }

  async function loadingStatus(action) {
    try {
      dispatch({
        type: "CHANGE_LOADING",
        payload: action,
      });
    } catch (error) {
      console.log(error.message);
    }
  }

  async function uploadFile(file) {
    // const formData = new FormData();
    // formData.append("avatar", file);
    // const res = await axios.post(`${baseURL}/books/cover-upload`, formData, {
    //   headers: {
    //     "content-type": "multipart/form-data",
    //   },
    // });
    // return res.data.code;
  }

  async function addCategory(inputs) {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
          Authorization: localStorage.token,
        },
      };

      const res = await axios.post(`${baseURL}/categories/add`, inputs, config);

      if (res.data.status) {
        fetchCategories();
      }
    } catch (error) {
      console.log(error.message);
    }
  }

  async function addBook(inputs) {
    //First move the file
    const formData = new FormData();

    formData.append("avatar", inputs["img_file"]);

    const res = await axios.post(`${baseURL}/books/cover-upload`, formData, {
      headers: {
        "content-type": "multipart/form-data",
        Authorization: localStorage.token,
      },
    });

    if (!res.data.error) {
      inputs.img_link = res.data.code;

      try {
        const config = {
          headers: {
            "Content-Type": "application/json",
            "Cache-Control": "no-cache",
            Pragma: "no-cache",
            Expires: "0",
            Authorization: localStorage.token,
          },
        };

        const res = await axios.post(`${baseURL}/books/add`, inputs, config);

        if (res.data.status) {
          // fetchBooksByUploader();
        }
      } catch (error) {
        console.log(error.message);
      }
    }
  }

  async function fetchBook(code) {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };

      const res = await axios.get(
        `${baseURL}/books/read_book?code=${code}`,
        config
      );

      if (res.data) {
        dispatch({
          type: "GET_BOOK",
          payload: res.data,
        });
      }
    } catch (error) {
      console.log(error.message);
    }
  }

  async function fetchBooks() {
    loadingStatus(true);

    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };

      const res = await axios.get(`${baseURL}/books`, config);

      if (res.data) {
        dispatch({
          type: "GET_BOOKS",
          payload: res.data,
        });
      }
    } catch (error) {
      console.log(error.message);
    }

    loadingStatus(false);
  }

  async function fetchBooksByUploader(id) {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
          Authorization: localStorage.token,
        },
      };

      const res = await axios.get(
        `${baseURL}/books/uploaders_books?id=${id}`,
        config
      );

      console.log(res.data);

      if (res.data) {
        dispatch({
          type: "GET_UPLOADER_BOOKS",
          payload: res.data,
        });
      }
    } catch (error) {
      console.log(error.message);
    }

    loadingStatus(false);
  }

  async function fetchCategories() {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };

      const res = await axios.get(`${baseURL}/categories`, config);

      if (res.data) {
        dispatch({
          type: "GET_CATEGORIES",
          payload: res.data,
        });
      }
    } catch (error) {
      console.log(error.message);
    }
  }

  async function searchBooksByCategory(id) {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };

      const res = await axios.get(
        `${baseURL}/books/read_books_category?id=${id}`,
        config
      );

      let booksbyCategory = [];

      if (res.data != null) {
        booksbyCategory = res.data;
      }

      dispatch({
        type: "SET_BOOKS_TO_SEARCH_BY_CATEGORY",
        payload: booksbyCategory,
      });
    } catch (error) {
      console.log(error.message);
    }
  }

  async function fetchBooksByCategory(id, limit = 0) {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };

      const res = await axios.get(
        `${baseURL}/books/read_books_category?id=${id}`,
        config
      );

      let booksbyCategory = [];

      if (res.data != null) {
        booksbyCategory = res.data;
      }

      dispatch({
        type: "GET_BOOKS_BY_CATEGORY",
        payload: booksbyCategory,
      });
    } catch (error) {
      console.log(error.message);
    }
  }

  async function updateViews(id) {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };

      const res = await axios.get(
        `${baseURL}/books/add_clicks?book=${id}`,
        config
      );

      if (!res.data) {
        console.log("View for this book have been updated");
      }
    } catch (error) {
      console.log(error.message);
    }
  }

  function setGlobalInfo(info) {
    dispatch({
      type: "SET_CLIENT_INFO",
      payload: info,
    });
  }

  function checkLoginStatus() {
    let login = false;

    if (localStorage.PLUD) {
      const info = readJwt(decryptData(localStorage.PLUD));
      login = true;
      setGlobalInfo(info);
    }

    dispatch({
      type: "CLIENT_LOGIN_STATUS",
      payload: login,
    });
  }

  async function userLogin(inputs) {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };

      const res = await axios.post(`${baseURL}/client/login`, inputs, config);

      if (!res.data.error) {
        localStorage.PLUD = encryptData(res.data.token);

        setTimeout(function () {
          checkLoginStatus();
        }, 200);
      }
    } catch (error) {}
  }

  async function userRegistration(inputs) {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };

      const res = await axios.post(
        `${baseURL}/client/register`,
        inputs,
        config
      );

      if (!res.data.error) {
        localStorage.PLUD = encryptData(res.data.token);

        setTimeout(function () {
          checkLoginStatus();
        }, 200);
      }
    } catch (error) {}
  }

  async function handleLogout() {
    localStorage.clear();

    setTimeout(function () {
      checkLoginStatus();
    }, 200);
  }

  async function makePaymentMpesa(inputs) {
    try {
      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };

      // Remember to add authorization

      console.log(inputs);

      const res = await axios.post(`${baseURL}/client/payment`, inputs, config);

      if (!res.data.error) {
        localStorage.PLUD = encryptData(res.data.token);

        const redirect = localStorage._actRed;

        if (!_.isEmpty(redirect)) {
          history.push(redirect.replace(/['"]+/g, ""));
        } else {
          history.push("/");
        }

        setTimeout(() => {
          localStorage.removeItem("_actRed");

          checkLoginStatus();
        }, 10);
      }
    } catch (error) {}
  }

  useEffect(() => {
    checkLoginStatus();
    loginStatus();

    // eslint-disable-next-line
  }, []);

  return (
    <GlobalContext.Provider
      value={{
        addBook,
        fetchBook,
        fetchBooks,
        fetchCategories,
        fetchBooksByCategory,
        updateViews,
        loadingStatus,
        loginAdmin,
        logOutAdmin,
        uploadFile,
        addCategory,
        fetchBooksByUploader,
        userRegistration,
        userLogin,
        checkLoginStatus,
        handleLogout,
        makePaymentMpesa,
        searchBooksByCategory,

        registration: state.registration,
        isLoggedIn: state.isLoggedIn,
        loading: state.loading,
        uploader_books: state.uploader_books,
        book: state.book,
        books: state.books,
        books_by_category: state.books_by_category,
        categories: state.categories,
        client_isLoggedIn: state.client_isLoggedIn,
        client_info: state.client_info,
      }}
    >
      {children}
    </GlobalContext.Provider>
  );
};
