import React, { useState, useEffect, useContext } from "react";
import { GlobalContext } from "../context/GlobalState";
import { Link } from "react-router-dom";
import Navigation from "../components/layout/Navigation";
import Footer from "../components/layout/Footer";
import Subscribe from "../components/section/Subscribe";
import Pagination from "../components/section/Pagination";
import { sanitizeBookName } from "../helpers/Functions";
import BookImage from "../components/section/BookImage";

const Categories = (props) => {
  const {
    books_by_category,
    categories,
    fetchCategories,
    fetchBooksByCategory,
  } = useContext(GlobalContext);

  const [loading, setloading] = useState(false);

  useEffect(() => {
    setloading(true);

    fetchCategories();
    fetchBooksByCategory(props.match.params.category_id);

    setTimeout(() => {
      setloading(false);
    }, 500);

    // eslint-disable-next-line
  }, [props.match.params.category_id]);

  return (
    <>
      <div className="bg-grey fl-wdt-hgt">
        <Navigation />
        <div className="content-opt-sz py-3 px-1 bg_bdr">
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-3 col-lg-3 col-12">
                {/* <div className="category_listing_mobile d-md-none">
                  <h6 className="mb-3">
                    <b>Filter by Category</b>
                  </h6>
                  <select>
                    {categories
                      .sort((a, b) => a.name.localeCompare(b.name))
                      .map((c, i) => (
                        <option key={i}>{c.name}</option>
                      ))}
                  </select>
                  <div className="results"></div>
                </div> */}
                <ul className="category_list">
                  {categories
                    .sort((a, b) => a.name.localeCompare(b.name))
                    .map((c, i) => (
                      <li key={i}>
                        <Link
                          className="active"
                          to={`/category/${c.id}/${sanitizeBookName(c.name)}`}
                        >
                          {c.name}
                        </Link>
                      </li>
                    ))}
                </ul>
              </div>
              <div className="col-12 col-md-9">
                {/* <div className="row">
                  <div className="col-12 col-md-12">
                    <h5>{props.match.params.category_name}</h5>
                    <hr />
                  </div>
                </div> */}
                {loading ? (
                  <>Please wait ...</>
                ) : (
                  <>
                    {books_by_category.length < 1 ? (
                      <>Hakuna kitu</>
                    ) : (
                      <>
                        {books_by_category.map((b, i) => (
                          <div key={i} className="category_listing">
                            <div className="row">
                              <div className="col-md-3 col-6">
                                <div className="book-cover book-category-hgt">
                                  <Link to={`/book/${b.code}/${b.title}`}>
                                    <BookImage img_link={b.img_link} />
                                  </Link>
                                </div>
                              </div>
                              <div className="col-md-9 col-6">
                                <h4>
                                  <u>{b.title}</u>
                                  {b.subtitle ? (
                                    <>
                                      <br />
                                      <i>{b.subtitle}</i>
                                    </>
                                  ) : (
                                    ""
                                  )}
                                  <br />
                                  <span>by {b.author}</span>
                                </h4>
                                <p>
                                  {b.description.slice(0, 300)}...
                                  <br /> <br />
                                  <Link
                                    to={`/book/${b.code}/${sanitizeBookName(
                                      b.title
                                    )}`}
                                  >
                                    Download eBook
                                  </Link>
                                </p>
                              </div>
                            </div>
                          </div>
                        ))}
                        <Pagination books={books_by_category} limit="24" />
                      </>
                    )}
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
        <Subscribe />
        <Footer />
      </div>
    </>
  );
};

export default Categories;
