import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCalendarAlt,
  faAddressCard,
  faUserLock,
} from "@fortawesome/free-solid-svg-icons";
import { useForm } from "react-hook-form";

const Visa = () => {
  const { register, handleSubmit, errors, reset } = useForm();

  const onSubmit = (data) => {
    console.log(data);
  };

  return (
    <form autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
      <div className="row">
        <div className="col-6 col-md-12">
          <label>Cardholder's Name</label>
          <div className="pay-box lng-inpt">
            <span>
              <FontAwesomeIcon icon={["fab", "cc-visa"]} />
            </span>
            <input
              ref={register({ required: true })}
              type="text"
              name="card-name"
              placeholder=""
            />
          </div>
        </div>
        <div className="col-6 col-md-12">
          <label>Card Number</label>
          <div className="pay-box lng-inpt">
            <span>
              <FontAwesomeIcon icon={faAddressCard} />
            </span>
            <input
              ref={register({ required: true })}
              type="text"
              name="card-number"
              placeholder=""
            />
            &nbsp;
          </div>
        </div>
        <div className="col-6 col-md-4">
          <label>
            {/* <FontAwesomeIcon icon={faCalendarAlt} /> */}
            Card Expiry date
          </label>
          <div className="pay-box shrt-inpt">
            <input
              ref={register({ required: true })}
              type="text"
              name="card-expiry"
              placeholder="MM/YY"
            />
            &nbsp;
          </div>
        </div>
        <div className="col-6 col-md-4">
          <label>
            {/* <FontAwesomeIcon icon={faUserLock} /> */}
            Card Security Code
          </label>
          <div className="pay-box shrt-inpt">
            <input
              ref={register({ required: true })}
              type="text"
              name="card-cvv"
              placeholder="CVV"
            />
            &nbsp;
          </div>
        </div>
        <div className="col-6 col-md-4">
          <p>
            * CVV or CVC is the card security code, unique three digits number
            on the back of your card separate from its number.
          </p>
        </div>
      </div>
      <div className="row">
        <div className="col-6 col-md-6">
          <button type="submit">Complete</button>
        </div>
      </div>
    </form>
  );
};

export default Visa;
