import React, { useContext } from "react";
import { GlobalContext } from "../context/GlobalState";
import { Link } from "react-router-dom";
import { sanitizeBookName } from "../helpers/Functions";
import BookImage from "../components/section/BookImage";

const Books = ({ books }) => {
  const { updateViews } = useContext(GlobalContext);

  return (
    <>
      {books.map((b, i) => (
        <div key={i} className="col-md-2 col-6">
          <div className="book-cover book-sm-hgt">
            <Link to={`/book/${b.code}/${sanitizeBookName(b.title)}`}>
              <BookImage img_link={b.img_link} />
            </Link>
          </div>
          <div className="book-text">
            <Link
              to={`/book/${b.code}/${sanitizeBookName(b.title)}`}
              onClick={() => updateViews(b.id)}
            >
              {b.title}
            </Link>
          </div>
        </div>
      ))}
    </>
  );
};

export default Books;
