import React, { useState, useContext } from "react";
import Packages from "../components/section/Packages";
import Payment from "../components/section/Payment";
import { Link } from "react-router-dom";
import { ChevronRight } from "react-feather";
import { GlobalContext } from "../context/GlobalState";

const Pricing = () => {
  const { client_isLoggedIn } = useContext(GlobalContext);

  const [state, setstate] = useState({
    subscription: {
      text: "Select subscription",
      payment: 0,
      price: 0,
    },
  });

  const updateSubscription = (payment = 1, price = 0) => {
    setstate({
      ...state,
      subscription: {
        text: payment > 0 ? "Make payment" : "Select subscription",
        payment,
        price,
      },
    });
  };

  const { subscription } = state;

  return (
    <div className="bg-super fl-wdt-hgt">
      <div className="content-opt-sz">
        <div className="pricing-table">
          <div className="top-pricing-intro">
            <h2>{subscription.text}</h2>
            {client_isLoggedIn && (
              <Link to="/">
                Skip this step&nbsp;
                <ChevronRight size={15} />
              </Link>
            )}
          </div>
          <div className="pricing-boxes">
            <Packages
              subscription={subscription}
              updateSubscription={updateSubscription}
              client_isLoggedIn={client_isLoggedIn}
            />
            {subscription.payment && <Payment subscription={subscription} />}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Pricing;
