import React, { useEffect, useState, useContext } from "react";
import { GlobalContext } from "../context/GlobalState";
import Footer from "../components/layout/Footer";
import Navigation from "../components/layout/Navigation";
import { Link } from "react-router-dom";
import CategorySection from "../components/section/CategorySection";
import Books from "../pages/Books";
import Subscribe from "../components/section/Subscribe";
import {
  sanitizeBookName,
  shortenString,
  getNumberofWords,
} from "../helpers/Functions";
import {
  ChevronDown,
  ChevronRight,
  ArrowRight,
  ChevronUp,
  BookOpen,
} from "react-feather";
import BookImage from "../components/section/BookImage";
import { useHistory } from "react-router-dom";

const SingleBook = (props) => {
  const history = useHistory();

  const {
    client_isLoggedIn,
    client_info,
    books,
    book,
    categories,
    fetchCategories,
    fetchBook,
    fetchBooksByCategory,
  } = useContext(GlobalContext);

  const [descMax, setdescMax] = useState(700);

  const [loading, setloading] = useState(false);

  const [state, setstate] = useState({
    active_category: null,
  });

  const limitToMoreBooks = (category_id) => {
    let more_books_from_category = books
      .filter(function (book) {
        return book.category_id === parseInt(category_id);
      })
      // eslint-disable-next-line eqeqeq
      .filter((item) => item.id != book.id)
      .slice(0, 6);

    if (more_books_from_category.length > 0) {
      return <Books books={more_books_from_category} />;
    } else {
      return <>No books from this category</>;
    }
  };

  useEffect(() => {
    setloading(true);

    fetchBook(props.match.params.code);
    fetchBooksByCategory(state.active_category);
    fetchCategories();

    setTimeout(() => {
      setloading(false);
    }, 500);

    return () => {
      setloading(false);
    };

    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    setloading(true);

    fetchBooksByCategory(state.active_category);
    fetchBook(props.match.params.code);

    setTimeout(() => {
      setloading(false);
    }, 1000);

    return () => {
      setloading(false);
    };

    // eslint-disable-next-line
  }, [props.match.params.code]);

  return (
    <>
      <div className="bg-grey fl-wdt-hgt" id="startHere">
        <Navigation />
        <div className="content-opt-sz py-3 px-1 bg_bdr">
          <div className="container-fluid">
            {loading ? (
              <>
                <div className="loading">Loading</div>
              </>
            ) : (
              <>
                <section className="single_book">
                  <div className="row">
                    <div className="col-md-12 col-6">
                      <div className="bread-crumb">
                        <p>
                          <Link to="">Home&nbsp;</Link>
                          <span>
                            <ChevronRight size="15" />
                          </span>
                          <Link
                            to={`/category/${book.category}/${book.category_name}`}
                          >
                            {book.category_name}
                          </Link>
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-md-4 col-8">
                      <div className="book-cover book-singlepage-hgt">
                        <Link to={`/book/${book.code}/${book.title}`}>
                          <BookImage img_link={book.img_link} />
                        </Link>
                      </div>
                    </div>
                    <div className="col-md-8 col-12 on-mobile-push">
                      <h3>
                        {book.title}
                        <span>
                          {book.subtitle ? (
                            <>
                              <br />
                              <i>{book.subtitle}</i>
                            </>
                          ) : (
                            ""
                          )}
                        </span>
                        <br />
                        <span>&nbsp;by&nbsp;{book.author}</span>
                      </h3>
                      <p>
                        {getNumberofWords(`${book.description}`) > descMax ? (
                          <>
                            {shortenString(`${book.description}`, descMax)}
                            <button onClick={() => setdescMax(100000)}>
                              ...read more
                              <ChevronDown size={17} />
                            </button>
                          </>
                        ) : (
                          <>
                            {book.description}
                            {getNumberofWords(`${book.description}`) > 740 && (
                              <button onClick={() => setdescMax(550)}>
                                ...read less
                                <ChevronUp size={17} />
                              </button>
                            )}
                          </>
                        )}
                      </p>
                      {client_isLoggedIn ? (
                        <>
                          {client_info.account_status > 0 ? (
                            <div className="book_download">
                              <button
                                onClick={() =>
                                  history.push(
                                    `/read/${props.match.params.code}`
                                  )
                                }
                              >
                                <BookOpen />
                                &nbsp;&nbsp;Read Book
                              </button>
                            </div>
                          ) : (
                            <div className="book_link bg-yellow">
                              <h5>Download link is unavailable</h5>
                              <div className="links_holder">
                                <Link
                                  to="/subscription"
                                  onClick={() =>
                                    (localStorage._actRed = JSON.stringify(
                                      `book/${
                                        props.match.params.code
                                      }/${sanitizeBookName(book.title)}`
                                    ))
                                  }
                                >
                                  Activate subscription <ArrowRight size={15} />
                                </Link>
                              </div>
                            </div>
                          )}
                        </>
                      ) : (
                        <div className="book_link bg-yellow">
                          <h5>Get unlimited access to read this book</h5>
                          <div className="links_holder_before">
                            <Link
                              to="/login"
                              onClick={() =>
                                (localStorage._actRed = JSON.stringify(
                                  `book/${
                                    props.match.params.code
                                  }/${sanitizeBookName(book.title)}`
                                ))
                              }
                            >
                              Login
                            </Link>
                            <Link
                              to="/register"
                              onClick={() =>
                                (localStorage._actRed = JSON.stringify(
                                  `book/${
                                    props.match.params.code
                                  }/${sanitizeBookName(book.title)}`
                                ))
                              }
                            >
                              Register
                            </Link>
                          </div>
                        </div>
                      )}
                      <div className="more_book_info">
                        <ul>
                          <li>
                            <span>Format:</span>
                          </li>
                          <li>
                            <span>Pages:</span>
                          </li>
                          <li>
                            <span>Size:</span>
                          </li>
                          <li>
                            <span>Language:</span>
                          </li>
                        </ul>
                        <ul>
                          <li>{book.formart}</li>
                          <li>{book.pages}</li>
                          <li>3MB</li>
                          <li>English</li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </section>
                {/* <section className="more-info">
                  <span>
                    Lorem ipsum dolor sit amet, consectetur adipisicing elit,
                    sed do eiusmod tempor incididunt ut labore et dolore magna
                    aliqua. Ut enim ad minim veniam, quis nostrud exercitation
                    ullamco laboris nisi ut aliquip ex ea commodo consequat.
                    Duis aute irure dolor in reprehenderit in voluptate velit
                    esse cillum dolore eu fugiat nulla pariatur. Excepteur sint
                    occaecat cupidatat non proident, sunt in culpa qui officia
                    deserunt mollit anim id est laborum.
                  </span>
                </section> */}
                <section className="book-content">
                  <br />
                  <div className="row">
                    <div className="col-md-12">
                      <h4>More&nbsp;{book.category_name}&nbsp;titles...</h4>
                      <hr />
                    </div>
                  </div>
                  <br />
                  {/* <div className="row">
                <div className="col-md-12">
                  <h6>My recent viewed</h6>
                  <hr />
                </div>
              </div> */}
                  <div className="row">
                    {limitToMoreBooks(book.category_id)}
                  </div>
                </section>
              </>
            )}
          </div>
        </div>

        <Subscribe />
        <CategorySection categories={categories} />
        <Footer />
      </div>
    </>
  );
};

export default SingleBook;
