const AppReducer = (state, action) => {
  // console.log("state ", state);
  // console.log("action", action.type);
  // console.log("payload", action.payload);

  switch (action.type) {
    case "SET_CLIENT_INFO":
      return {
        ...state,
        client_info: action.payload,
      };
    case "CLIENT_LOGIN_STATUS":
      return {
        ...state,
        client_isLoggedIn: action.payload,
      };
    case "CHANGE_LOGIN_STATUS":
      return {
        ...state,
        isLoggedIn: action.payload,
      };
    case "CHANGE_LOADING":
      return {
        ...state,
        loading: action.payload,
      };
    case "GET_BOOK":
      return {
        ...state,
        book: action.payload,
      };
    case "GET_BOOKS":
      return {
        ...state,
        books: action.payload,
      };
    case "GET_BOOKS_BY_CATEGORY":
      return {
        ...state,
        books_by_category: action.payload,
      };
    case "GET_UPLOADER_BOOKS":
      return {
        ...state,
        uploader_books: action.payload,
      };
    case "GET_CATEGORIES":
      return {
        ...state,
        categories: action.payload,
      };
    case "SET_SUBSCRIPTION":
      return {
        ...state,
        subscription: action.payload,
      };
    case "SET_BOOKS_TO_SEARCH_BY_CATEGORY":
      return {
        ...state,
        search_list: action.payload,
      };

    default:
      return state;
  }
};

export default AppReducer;
