import React, { useContext } from "react";
import logo from "../images/logo/logo.png";
import { Link, withRouter, Redirect } from "react-router-dom";
import { useForm } from "react-hook-form";
import { GlobalContext } from "../context/GlobalState";
import _ from "lodash";

const Login = () => {
  const { client_isLoggedIn, client_info, userLogin } =
    useContext(GlobalContext);

  const { register, handleSubmit, errors, reset } = useForm();

  const onSubmit = (data) => {
    userLogin(data);
    reset();
  };

  if (client_isLoggedIn && typeof client_info !== "undefined") {
    let to = "/";

    const redirect = localStorage._actRed;

    if (!_.isEmpty(redirect)) {
      to = redirect.replace(/['"]+/g, "");

      setTimeout(() => {
        localStorage.removeItem("_actRed");
      }, 10);
    }

    return <Redirect to={to} />;
  } else {
    return (
      <div className="bg-super fl-wdt-hgt">
        <div className="content-opt-sz">
          <div className="user-section">
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-12">
                  <div className="logo">
                    <Link to="/">
                      Home
                      {/* <img src={logo} alt="LOGO" /> */}
                    </Link>
                  </div>
                </div>
              </div>
              <form autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
                <div className="row">
                  <div className="col-md-12">
                    <label>Email address</label>
                    <input
                      className="user-inputs"
                      name="email"
                      placeholder="Your email address"
                      type="text"
                      ref={register({ required: true })}
                    />
                    <span className="err_msg">
                      {errors.email && errors.email.message}
                    </span>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <label>Password</label>
                    <input
                      className="user-inputs"
                      name="password"
                      placeholder="Your password"
                      type="text"
                      ref={register({ required: true })}
                    />
                    <span className="err_msg">
                      {errors.conf_password && errors.conf_password.message}
                    </span>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <input
                      className="user-input-btn  hght-45"
                      type="submit"
                      value="LOGIN"
                    />
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <p>
                      Don't have an account?{" "}
                      <Link to="/register">Register</Link>
                    </p>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
};

export default withRouter(Login);
