import React, { useEffect, useContext } from "react";
import { GlobalContext } from "../context/GlobalState";
import Footer from "../components/layout/Footer";
import Navigation from "../components/layout/Navigation";
import Books from "./Books";
import Pagination from "../components/section/Pagination";
import CategorySection from "../components/section/CategorySection";
import Subscribe from "../components/section/Subscribe";
// import _ from "lodash";

const Home = () => {
  const { books, categories, fetchBooks, fetchCategories } =
    useContext(GlobalContext);

  const freeBooks = (items) => {
    let free = items.filter((book) => book.is_free < 2).slice(0, 28);
    // return <Books books={_.shuffle(free)} />;
    return <Books books={free} />;
  };

  // const mostDownloads = (items) => {
  //   let downloads = items.filter((book) => book.downloads > 2).slice(0, 6);
  //   return <Books books={downloads} />;
  // };

  useEffect(() => {
    fetchBooks();
    fetchCategories();

    return () => {
      window.scrollTo(0, 0);
    };

    // eslint-disable-next-line
  }, []);

  return (
    <>
      <div className="bg-grey fl-wdt-hgt">
        {/* <Announcment /> */}
        <Navigation />
        <div className="content-opt-sz py-4 px-1 bg_bdr home-styling">
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-6">
                <h3>Browse E-Books</h3>
              </div>
            </div>
            <hr />
            <div className="row">{freeBooks(books)}</div>
            <br />
            <Pagination books={books} limit="24" />
            {/* <div className="row">
              <div className="col-md-12">
                <h3>This weeks top downloads</h3>
                <hr />
              </div>
            </div>
            <div className="row">{mostDownloads(books)}</div> */}
          </div>
        </div>
        <Subscribe />
        <CategorySection categories={categories} />
        <Footer />
      </div>
    </>
  );
};

export default Home;
