import React, { useState } from "react";
import { addCommas } from "../../helpers/Functions";
import { ArrowRight, ArrowLeft } from "react-feather";
import { useHistory } from "react-router-dom";

const Packages = ({ updateSubscription, client_isLoggedIn }) => {
  const history = useHistory();

  const [state, setstate] = useState({
    chosen: null,
  });

  const setChosen = (chosen) => {
    setstate({
      chosen,
    });
  };

  const changePlan = (chosen) => {
    setstate({
      chosen: null,
    });

    updateSubscription(chosen);
  };

  const packages = [
    {
      id: 1,
      name: "PERSONAL",
      price: 499,
      features: [
        { feature: "Concurrent users: 1", suported: true },
        { feature: "1 month valid subscription", suported: true },
        { feature: "2 reads per week", suported: true },
        { feature: "Free support 24/7", suported: true },
        { feature: "Unlimited book requests", suported: true },
        { feature: "No Hidden Fees", suported: true },
        { feature: "No Advertising", suported: true },
      ],
    },
    {
      id: 2,
      name: "STANDARD",
      price: 1499,
      features: [
        { feature: "Concurrent users : 5", suported: true },
        { feature: "3 months valid subscription", suported: true },
        { feature: "5 reads per week", suported: true },
        { feature: "Free support 24/7", suported: true },
        { feature: "Unlimited book requests", suported: true },
        { feature: "No Hidden Fees", suported: true },
        { feature: "No Advertising", suported: true },
      ],
    },
    {
      id: 3,
      name: "LIFETIME",
      price: 5500,
      features: [
        { feature: "Concurrent users: Unlimited", suported: true },
        { feature: "1 year valid subscription", suported: true },
        { feature: "100 downloads per month", suported: true },
        { feature: "Free support 24/7", suported: true },
        { feature: "Unlimited book requests", suported: true },
        { feature: "No Hidden Fees", suported: true },
        { feature: "No Advertising", suported: true },
      ],
    },
  ];

  const { chosen } = state;

  return (
    <>
      {chosen === null ? (
        <>
          {packages.map((p, i) => (
            <div className="box" key={i}>
              <h5>{p.name}</h5>
              <h1>
                <sup>KES</sup>
                {/* <sup>$</sup> */}
                &nbsp;{addCommas(p.price)}
              </h1>
              <hr />
              <ul>
                {p.features.map((f, i) => (
                  <li key={i}>
                    {/* <Check size={15} /> */}
                    {f.feature}
                  </li>
                ))}
              </ul>
              {client_isLoggedIn ? (
                <>
                  <button
                    className="choose"
                    onClick={() => {
                      setChosen(p.id);
                      updateSubscription(p.id, p.price);
                    }}
                  >
                    Choose plan&nbsp;
                    <ArrowRight size={16} />
                  </button>
                </>
              ) : (
                <button
                  className="choose"
                  onClick={() => {
                    history.push("/register");
                  }}
                >
                  Create Account&nbsp;
                  <ArrowRight size={16} />
                </button>
              )}
            </div>
          ))}
        </>
      ) : (
        <>
          {packages
            .filter((r, i) => r.id === chosen)
            .map((p, i) => (
              <div className="box" key={i}>
                <h5>{p.name}</h5>
                <h1>
                  <sup>KES</sup>
                  {/* <sup>$</sup> */}
                  &nbsp;{addCommas(p.price)}
                </h1>
                <hr />
                <ul>
                  {p.features.map((f, i) => (
                    <li key={i}>
                      {/* <Check size={15} /> */}
                      {f.feature}
                    </li>
                  ))}
                </ul>
                <button className="change" onClick={() => changePlan(0)}>
                  <ArrowLeft size={16} />
                  &nbsp;Select different plan
                </button>
              </div>
            ))}
        </>
      )}
    </>
  );
};

export default Packages;
