import React, { useContext } from "react";
import { GlobalContext } from "../context/GlobalState";
import Footer from "../components/layout/Footer";
import Navigation from "../components/layout/Navigation";
import { Redirect } from "react-router-dom";

const Profile = () => {
  const { client_isLoggedIn } = useContext(GlobalContext);

  if (client_isLoggedIn) {
    return (
      <>
        <div className="bg-grey fl-wdt-hgt" id="startHere">
          <Navigation />
          <div className="content-opt-sz py-3 px-1 bg_bdr">
            <div className="container-fluid profile_box">
              <div className="row">
                <div className="col-lg-3 col-md-3 col-6">
                  <div className="card"></div>
                </div>
                <div className="col-lg-9 col-md-9 col-6">
                  <ul>
                    <li>Information</li>
                    <li>Subscription</li>
                    <li>Uploads</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  } else {
    return <Redirect to="/" />;
  }
};

export default Profile;
