import React from "react";
import CategoryList from "./CategoryList";

const CategorySection = ({ categories }) => {
  const categoryList = (col) => {
    const number = categories.length / 4;
    let list = [];

    list = categories.filter(
      (c, i) =>
        i >= number.toFixed() * col - number.toFixed() &&
        i < number.toFixed() * col
    );

    return <CategoryList list={list} />;
  };
  return (
    <>
      <div className="content-opt-sz py-3 px-1 bg_bdr categories">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-12">
              <h3 className="text-center">Popular categories</h3>
            </div>
          </div>
          <div className="mobile-categories">
            <div className="row">
              <div className="col-md-3 col-xs-6">{() => categoryList(1)}</div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <button>See full list</button>
              </div>
            </div>
          </div>
          <div className="desktop-categories">
            <div className="row">
              <div className="col-md-3 col-xs-6">
                <ul>{categoryList(1)}</ul>
              </div>
              <div className="col-md-3 col-xs-6">
                <ul>
                  <ul>{categoryList(2)}</ul>
                </ul>
              </div>
              <div className="col-md-3 col-xs-6">
                <ul>
                  <ul>{categoryList(3)}</ul>
                </ul>
              </div>
              <div className="col-md-3 col-xs-6">
                <ul>
                  <ul>{categoryList(4)}</ul>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CategorySection;
