import React, { useState } from "react";
import VisaImg from "../../images/logo/visa.jpg";
import MpesaImg from "../../images/logo/mpesa.png";
import MasterCardImg from "../../images/logo/master-card.png";
import PayPalImg from "../../images/logo/paypal.png";

import Visa from "../section/payment-methods/Visa";
import MasterCard from "../section/payment-methods/MasterCard";
import Mpesa from "../section/payment-methods/Mpesa";
import PayPal from "../section/payment-methods/PayPal";

const Payment = ({ subscription }) => {
  const [selectedMethod, setselectedMethod] = useState(1);

  return (
    <>
      <div className="payment-box">
        <div className="container-fluid">
          <div className="row">
            <div className="col-12 col-md-12">
              <h5>Choose a method of payment</h5>
            </div>
          </div>
          <div className="payment-options">
            <label for="mpesa" onClick={() => setselectedMethod(1)}>
              <div className="card-logos">
                <img src={MpesaImg} alt="" />
              </div>
              <div class="radio-input">
                Pay with Mpesa&nbsp;
                <input type="radio" name="mpesa" />
              </div>
            </label>
            {/* <label for="visa">
              <div className="card-logos">
                <img src={VisaImg} alt="" />
              </div>
              <div class="radio-input">
                Pay with Visa&nbsp; <input type="radio" name="visa" />
              </div>
            </label>
            <label for="master-card">
              <div className="card-logos">
                <img src={MasterCardImg} alt="" />
              </div>
              <div class="radio-input">
                Pay with Master Card&nbsp;
                <input type="radio" name="master" />
              </div>
            </label>
            <label for="paypal">
              <div className="card-logos">
                <img src={PayPalImg} alt="" />
              </div>
              <div class="radio-input">
                Pay with PayPal&nbsp; <input type="radio" name="paypal" />
              </div>
            </label> */}
            {/* <div className="row">
              <div className="col-6 col-md-3">
                <label>
                  <span>
                    <input type="checkbox" />
                  </span>
                </label>
              </div>
              <div className="col-6 col-md-3">
                <label>
                  <span>
                    <input type="checkbox" />
                  </span>
                </label>
              </div>
            </div> */}
          </div>
          {selectedMethod === 1 && <Mpesa amount={subscription.price} />}
          {selectedMethod === 2 && <Visa amount={subscription.price} />}
          {selectedMethod === 3 && <MasterCard amount={subscription.price} />}
          {selectedMethod === 4 && <PayPal amount={subscription.price} />}
        </div>
      </div>
    </>
  );
};

export default Payment;
