import Cookies from "js-cookie";
import jwt from "jwt-simple";
import CryptoJS from "crypto-js";

export const baseURL = "http://koob.policy-xpert.co.ke/v1/api";

export const imgURL = "http://koob.policy-xpert.co.ke/library";

// export const baseURL = "http://localhost:81/pdfbookstore/v1/api";

// export const imgURL = "http://localhost:81/pdfbookstore/library";

export const shortenString = (string, length = 0) => {
  return string.substring(0, length);
};

export const encryptData = (data) => {
  const ciphertext = CryptoJS.AES.encrypt(
    JSON.stringify(data),
    process.env.REACT_APP_SECRET
  ).toString();

  return ciphertext;
};

export const decryptData = (ciphertext) => {
  var bytes = CryptoJS.AES.decrypt(ciphertext, process.env.REACT_APP_SECRET);
  var decryptedData = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));

  return decryptedData;
};

export const addCookie = (payload, cookie_name) => {
  var date = new Date();
  var minutes = 30;
  date.setTime(date.getTime() + minutes * 60 * 10000);

  Cookies.set(cookie_name, payload, {
    expires: date,
  });
};

export const readJwt = (token) => {
  let value = [];
  const secret = "kwatoski";

  value = jwt.decode(token, secret);

  return value;
};

export const addCommas = (num) => {
  return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
};

export const sanitizeBookName = (name) => {
  return name.replace(/\s+/g, "-").toLowerCase();
};

export const getNumberofWords = (description) => {
  const words = description.length;

  return words;
};

export const capitalizeFirstLetter = (str) => {
  const capitalized = str.charAt(0).toUpperCase() + str.slice(1);

  return capitalized;
};
