import React from "react";
import { GlobalProvider } from "./context/GlobalState";
import { Route, Switch } from "react-router-dom";

import Home from "./pages/Home";
import Categories from "./pages/Categories";
import SingleBook from "./pages/SingleBook";
import Login from "./pages/Login";
import Register from "./pages/Register";
import Pricing from "./pages/Pricing";
import Profile from "./pages/Profile";
import ScrollToTop from "./ScrollToTop";
import NotFound404 from "./pages/page-errors/NotFound404";
import WrongCategoryURL from "./pages/page-errors/WrongCategoryURL";
import ReaderPDF from "./pages/ReaderPDF.js";

const App = () => {
  return (
    <GlobalProvider>
      <ScrollToTop />
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/login" exact component={Login} />
        <Route path="/register" exact component={Register} />
        <Route path="/subscription" exact component={Pricing} />
        <Route
          path="/category/:category_id/:category_name"
          exact
          component={Categories}
        />
        <Route
          exact
          path={"/category" || "/category/:category_id"}
          component={WrongCategoryURL}
        />
        <Route path="/book/:code/:title" exact component={SingleBook} />
        <Route path="/profile" exact component={Profile} />
        <Route path="/read/:code" exact component={ReaderPDF} />
        <Route component={NotFound404} />
      </Switch>
    </GlobalProvider>
  );
};

export default App;
