import React, { useContext } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMobileAlt } from "@fortawesome/free-solid-svg-icons";
import { useForm } from "react-hook-form";
import { GlobalContext } from "../../../context/GlobalState";

const Mpesa = ({ amount }) => {
  const { makePaymentMpesa, client_info } = useContext(GlobalContext);

  const { register, handleSubmit, errors, reset } = useForm();

  const onSubmit = (data) => {
    data["email"] = client_info.email;

    makePaymentMpesa(data);
  };

  return (
    <form autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
      <div className="row">
        <div className="col-6 col-md-12">
          <label>Enter your mobile number</label>
          <div className="pay-box lng-inpt">
            <span>
              <FontAwesomeIcon icon={faMobileAlt} />
            </span>
            <input
              ref={register({ required: true })}
              type="text"
              name="mobile"
              placeholder=""
            />
            <input
              type="hidden"
              name="amount"
              ref={register({ required: true })}
              value={amount}
            />
          </div>
        </div>

        <div className="col-12 col-md-8">
          <p>
            * Once entered a prompt should appear on your screen.
            <br />
            Enter your pin to complete the transaction.
          </p>
        </div>
      </div>
      <div className="row">
        <div className="col-6 col-md-6">
          <button type="submit">Complete</button>
        </div>
      </div>
    </form>
  );
};

export default Mpesa;
